.category-container {
    display: flex
}
.category-btn {
    background-color: #efeff0;
    cursor: pointer;
    font-weight: 700;
    margin: 2px;
    padding: 12px;
}
.category-btn.selected {
    background-color: #bdbdc1;
}
.products-container {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
}
.product-item {
    max-width: 300px;
    margin: 20px;
}
img {
    margin-bottom: auto;
    margin-top: auto;
    max-height: 300px;
    object-fit: contain;
    width: 100%;
}
@media (max-width: 400px) {
    .category-container {
        display: grid;
        grid-template-columns: repeat(2, 1fr)
    }
}